<style scoped>
.main
{
    height: 100px;
    min-width: 1510px;
    margin: 0;
    /* background-color: #292929; */
    overflow: hidden;
}

.el-menu
{
    /* color: #949494; */
    position: relative;
    top: 50%;
    right: 0;
    left: 0;
    width: 400px;
    display: table;
    margin: 0 auto;
    transform: translateY(-50%);
    border-right: solid 1px transparent;
}
.el-menu-item.is-active {
    color: transparent;
}
.el-menu-item:focus, .el-menu-item:hover {
    outline: 0;
    background-color: transparent;
}
.el-menu-item
{
    position: relative;
    width: 25%;
    display: table-cell;
    text-align: center;
    color: #949494;
    text-decoration: none;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: bold;
    padding: 10px 20px;
    transition: 0.2s ease color;
}
a {
    color: #949494;
}

.el-menu-item:before, .el-menu-item:after
{
    content: "";
    position: absolute;
    border-radius: 50%;
    transform: scale(0);
    transition: 0.2s ease transform;
}

.el-menu-item:before
{
    top: 0;
    left: 10px;
    width: 6px;
    height: 6px;
}

.el-menu-item:after
{
    top: 5px;
    left: 18px;
    width: 4px;
    height: 4px
}

.el-menu-item:nth-child(1):before
{
    background-color: yellow;
}

.el-menu-item:nth-child(1):after
{
    background-color: red;
}

.el-menu-item:nth-child(2):before
{
    background-color: #00e2ff;
}

.el-menu-item:nth-child(2):after
{
    background-color: #89ff00;
}

.el-menu-item:nth-child(3):before
{
