    width: 30px;
    height: 3px;
    background-color: #fff;
    border-radius: 5px;
    transition: 0.2s ease left;
}

a:hover
{
    color: #000;
}
.el-menu-item:hover:before, .el-menu-item:hover:after
{
    transform: scale(1);
}

.el-menu-item:nth-child(1):hover ~ #indicator
{
    background: linear-gradient(130deg, yellow, red);
}

.el-menu-item:nth-child(2):hover ~ #indicator
{
    left: 34%;
    background: linear-gradient(130deg, #00e2ff, #89ff00);
}

.el-menu-item:nth-child(3):hover ~ #indicator
{
    left: 60%;
