<!--  -->
<template>
<div class='main'>
    <classify/>
    <div v-for="(item, index) in text" :key="index" >
      <!-- iv-for -->
      <!-- :title="item.title" :type="item.type" :brief="item.brief" :time="item.time" :image="item.image" -->
      <dynamics :msg="item"/>
    </div>
    <pagination/>
    <div class="block"></div>
</div>
</template>

<script>
// jsjsjson
// import '' from '';
import dynamics from '../../components/dynamicsGuide/dynamics'
import classify from '../../components/dynamicsGuide/classify'
import pagination from '../../components/dynamicsGuide/pagination'
import { baseUrl } from '../../assets/js/util.js'
import { showLoading, hideLoading } from '../../assets/js/loading'

import axios from 'axios'
axios.defaults.withCredentials = true
// import axios from 'axios'
export default {
  name: 'ourDynamics',
  // import
  components: {
    dynamics,
    classify,
    pagination
  },
  data () {
    // 
    return {
      text:
      [
        // {
        //   title: '()', // 
        //   type: '', // 
        //   brief: '.', // 
        //   time: '2019-10-23', // 
        //   image: '',
        //   appear: false // false 
        // },
        // {
        //   title: '()', // 
        //   type: '', // 
        //   brief: '.', // 
        //   time: '2019-10-23', // 
        //   image: '',
        //   appear: false // false 
        // },
        // {
        //   title: '()', // 
        //   type: '', // 
        //   brief: '.', // 
        //   time: '2019-10-23', // 
        //   image: '',
        //   appear: false // false 
        // },
        // {
        //   title: '()', // 
        //   type: '', // 
        //   brief: '.', // 
        //   time: '2019-10-23', // 
        //   image: '',
        //   appear: false // false 
        // },
        // {
        //   title: '()', // 
        //   type: '', // 
        //   brief: '.', // 
        //   time: '2019-10-23', // 
        //   image: ''
        //   // appear: false // false 
        // }
      ]
    }
  },
  //  data
  computed: {},
  beforeRouteUpdate (to, from, next) {
    // 
    //  /foo/:id /foo/1  /foo/2 
    //  Foo 
    //  `this`
    // console.log(to)
    // console.log(from)

    let type = to.params.type
    // console.log('' + type)
    this.getClassify(type)
    next() // 
  },
  // data
  watch: {
    // `this.$route.params.type`: {
    //   this.getClassify()
    // }
  },
  // 
  filters: {},
  // 
  methods: {
    getClassify (type) {
      showLoading()
      console.log(type)
      let that = this
      // let type = that.$route.params.type //  all   news   blogs  games
      that.classify = type
      axios({
        method: 'get',
        baseURL: baseUrl,
        url: '/getDynamicStateByType',
        // 'timeout'
        timeout: 30000,
        params: {
          type
        }
      }).then(function (response) {
        hideLoading()
        if (response.data.status === 'success') {
          that.text = response.data.data
          that.text.forEach((element, index) => {
            element.image = 'data:image/jpg;base64,' + element.image
          })
          console.log(that.text)
          // that.text.image = 'data:image/jpg;base64,' + response.data.data
          //  this.codeImg = 'data:image/jpg;base64,' + response.data.data
        }
      }).catch(function (error) { // 
        console.log(error)
      })
    }
  },
  //  - this
  created () {
    let type = this.$route.params.type //  all   news   blogs  games
    this.getClassify(type)
  },
  //  - DOM
  mounted () {

  },
  beforeCreate () {}, //  - 
  beforeMount () {}, //  - 
  beforeUpdate () {}, //  - 
  updated () {}, //  - 
  beforeDestroy () {}, //  - 
  destroyed () {}, //  - 
  activated () {} // keep-alive
}
</script>
<style scoped>
.block {
  width: 100%;
  height: 30px;
  background: transparent;
}
</style>
