<!--    -->
<template>
  <div class="exterior">
    <el-container>
      <el-header  v-if="show">
        <app-header></app-header>
      </el-header>
      <el-main>
        <router-view></router-view>
      </el-main>
      <el-footer  v-if="show">
        <app-footer></app-footer>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
// jsjsjson
// import '' from '';
import header from '../components/headerGuide/header'
import footer from '../components/footerGuide/footer'
export default {
// import
  components: {
    'app-header': header,
    'app-footer': footer
  },
  data () {
    // 
    return {
      show: true
    }
  },
  //  data
  computed: {},
  // data
  watch: {
    $route (to, from) {
      console.log(this.$route.path.slice(1))
      if (this.$route.path.slice(1) === '403') {
        this.show = false
      } else {
        this.show = true
      }
    }
  },
  // 
  filters: {},
  // 
  methods: {
  },
  //  - this
  created () {
    if (this.$route.path.slice(1) === 'error') {
      this.show = false
    } else {
      this.show = true
    }
  },
  //  - DOM
  mounted () {

  },
  beforeCreate () {}, //  - 
  beforeMount () {}, //  - 
  beforeUpdate () {}, //  - 
  updated () {}, //  - 
  beforeDestroy () {}, //  - 
  destroyed () {}, //  - 
  activated () {} // keep-alive
}
</script>
<style scoped>
#app .el-header,
#app .el-main,
#app .el-footer {
  padding: 0;
}
.content {
  min-width: 1200px;
  margin: 0 auto;
}
