<!--  -->
<template>
<div class='main'>
   <el-carousel :interval="4000" arrow="never"  ref="swiperWrap" :style='`width: 100%; height: ${config.height}px;`' autoplay>
    <el-carousel-item v-for="item in images" :key="item.id" :style='`width: 100%; height: ${config.height}px;`'>
      <img :src="item.idView?item.idView:require('../../assets/img/noimg.jpg')" alt="" class="img-div" :style='` height: 100%; width: 100%; background-size: cover;`'>
    </el-carousel-item>
  </el-carousel>
</div>
</template>

<script>
// jsjsjson
// import '' from '';
import { showLoading, hideLoading } from '../../assets/js/loading'

export default {
// import
  components: {},
  data () {
    // 
    return {
      images: [
        { id: 0, idView: require('../../assets/img/rotation3.jpg') },
        { id: 1, idView: require('../../assets/img/rotation1.png') },
        { id: 2, idView: require('../../assets/img/rotation4.jpg') }
      ],
      // imgHeight: '',
      config: {
        height: ''
      }
    }
  },
  //  data
  computed: {},
  // data
  watch: {},
  // 
  filters: {},
  // 
  methods: {
    resetHeight () {
      // const height = window.screen.height - 60
      const height = window.screen.availHeight - 160
      // console.log(height)
      this.config.height = height
    }
  },
  beforeCreate () {
    showLoading()
  },
  //  - this
  //  - DOM
  mounted () {
    // this.imgHeight = document.body.clientHeight
    hideLoading()
    const $this = this
    this.resetHeight()
    window.addEventListener('resize', () => {
      $this.resetHeight()
    })
  },
  beforeMount () {}, //  - 
  beforeUpdate () {}, //  - 
  updated () {}, //  - 
  beforeDestroy () {}, //  - 
  destroyed () {}, //  - 
  activated () {} // keep-alive
}
</script>
<style scoped>
  .img-div{
    width:100%;
    /* height:400px; */
  }
</style>
